.glitch {
  position: relative;
  display: table;
  margin-left: auto;
  margin-right: auto;
  color: inherit;
  position: relative;
}

@keyframes glitch-anim-1 {
  0% {
    clip: rect(9px, 99999px, 66px, 0);
  }
  20% {
    clip: rect(59px, 99999px, 23px, 0);
  }
  40% {
    clip: rect(49px, 99999px, 84px, 0);
  }
  60% {
    clip: rect(30px, 99999px, 89px, 0);
  }
  80% {
    clip: rect(33px, 99999px, 2px, 0);
  }
  100% {
    clip: rect(45px, 99999px, 72px, 0);
  }
}

@keyframes glitch-anim-2 {
  0% {
    clip: rect(4px, 99999px, 23px, 0);
  }
  20% {
    clip: rect(21px, 99999px, 87px, 0);
  }
  40% {
    clip: rect(27px, 99999px, 34px, 0);
  }
  60% {
    clip: rect(19px, 99999px, 26px, 0);
  }
  80% {
    clip: rect(17px, 99999px, 46px, 0);
  }
  100% {
    clip: rect(6px, 99999px, 22px, 0);
  }
}

.glitch:before, .glitch:after {
  content: attr(data-text);
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  background: #17161c;
  clip: rect(0, 0, 0, 0);
}

.glitch:after {
  left: 2px;
  text-shadow: -1px 0 red;
  animation: glitch-anim-1 2s infinite linear alternate-reverse;
}

.glitch:before {
  left: -2px;
  text-shadow: 2px 0 #7534ff;
  animation: glitch-anim-2 2s infinite linear alternate-reverse;
}

