html, body {
	height: 100%;
	overflow: hidden; }
body {
	background-color: $section-bg-dark;
	line-height: 1.4;
	font-size: 18px;
	@include roboto-reg;
	color: #fff;
	&.is-hidden {
		overflow: hidden; } }
a {
	text-decoration: none;
	color: inherit;
	@include roboto-reg;
	@include trans; }

//title
.title-primary {
	@include roboto-light;
	@include css-lock(60, 120, 900, 1920);
	line-height: 1;
	@include css-lock(30, 120, 600, 1920, 'margin-bottom');
	// margin-bottom: 120px
	@include sm {
 }		// margin-bottom: 40px
	@include xs {
		font-size: 32px; }
	&_centered {
		text-align: center; } }
.title-secondary {
	line-height: 1;
	@include roboto-med;
	@include css-lock(32, 48, 900, 1920);
	@include xs {
		font-size: 28px; } }

//icons
.icon-check {
	font-size: 32px;
	fill: #fff; }
.icon-close {
	font-size: 15px;
	fill: #0ca8fd; }
.icon-change {
	font-size: 21px;
	fill: rgba(#fff, 0.4); }
.icon-donut {
	@include css-lock(240, 410, 900, 1920);
	fill: #fff; }
[class^="i-icon"] {
	display: inline-block;
	vertical-align: middle; }
.i-icon-money {
	@include s(money); }
.i-icon-api {
	@include s(api); }
.i-icon-card {
	@include s(card); }
.i-icon-graph {
	@include s(graph); }
.i-icon-guard {
	@include s(guard); }
.i-icon-help {
	@include s(help); }
.i-icon-link {
	@include s(link); }
.i-icon-profile {
	@include s(profile); }
.i-icon-sale {
	@include s(sale); }
.i-icon-worker {
	@include s(worker); }
.i-icon-discount {
	@include s(discount); }
.i-icon-help-new {
	@include s(help-new); }
.i-icon-ruble {
	@include s(ruble); }

//flags
.i-icon-ru {
	@include s(ru); }
.i-icon-sw {
	@include s(sw); }
.i-icon-rum {
	@include s(rum); }
.i-icon-uk {
	@include s(uk); }

//  ie <= 9
.opened#reject .info {
	min-width: 650px; }

